/*-------------- Sass Modules ----------------*/
@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@400;500&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-size: 1rem;
  font-weight: 400;
  font-family: "Nunito", sans-serif;
  line-height: 1.5;
}

/*==== Mixin for button component ====*/
